import badgeComponent from "@/components/badgeComponent.vue"
import btnComponent from "@/components/btnComponent.vue"
import modalComponent from "@/components/modalComponent.vue"
export default {
	components: {
		badgeComponent,
		modalComponent,
		btnComponent
	},
	data() {
		return {
			list: [],
			curHeadwear: {} // 预览的头饰
		};
	},
	onLoad() {
		this.loadData()
	},
	onShow() {

	},
	onShareAppMessage(e) {
		const shareType = e.target && e.target.dataset.share
		return this.$app.commonShareAppMessage(shareType)
	},
	methods: {
		// 预览头饰
		preHead(e) {
			this.curHeadwear = e.currentTarget.dataset.cur
		},
		// 购买头饰
		buy(e) {
			wx.showModal({
				title: '提示',
				content: '是否兑换？',
				success: res => {
					if (res.confirm) {
						this.$app.request('headwear/buy', {
							hid: e.currentTarget.dataset.hid
						}, (res) => {
							this.$app.toast(res.msg)
							this.loadData()
						}, 'POST', true)
					}
				}
			})
		},
		use(e) {
			this.$app.request('headwear/use', {
				hid: e.currentTarget.dataset.hid
			}, (res) => {
				this.$app.toast(res.msg)
				this.loadData()
			}, 'POST', true)
		},
		cancel(e) {
			this.$app.request('headwear/cancel', {}, (res) => {
				this.$app.toast(res.msg)
				this.loadData()
			}, 'POST', true)
		},
		loadData() {
			this.$app.request('headwear/select', {}, res => {

				this.list = res.data.list
				this.curHeadwear = res.data.cur || null
			})
		}
	}
}
